import JobBanner from '@/components/job/JobBanner'
import { ArrowRight } from 'lucide-react'
import Link from 'next/link'
import React from 'react'

const page = () => {
    return (
        <>
            <JobBanner />
            <section className="p-4 border-borderColor border-[2px] rounded-lg my-6">
                <h2 className='section-title mb-2'>Advantages</h2>
                <div className="flex flex-col gap-2 ml-2">
                    <span className='text-lg leading-6 font-semibold  flex items-center gap-2'>flexible schedule</span>
                    <span className='text-lg leading-6 font-semibold  flex items-center gap-2'>tips + bonuses</span>
                    <span className='text-lg leading-6 font-semibold  flex items-center gap-2'>work in your own transport or ours</span>
                    <span className='text-lg leading-6 font-semibold  flex items-center gap-2'>no experience required</span>
                    <span className='text-lg leading-6 font-semibold  flex items-center gap-2'>flexible schedule</span>
                </div>
            </section>
        </>

    )
}

export default page